
 document.addEventListener("DOMContentLoaded", function(event) {
    //código a ejecutar cuando el DOM está listo para recibir acciones
    let boton = document.getElementById('btn-terminos');
    let contenedor = document.getElementById('ctn-term');
    let form = document.getElementById("my-form");
    let status = document.getElementById("my-form-status");
    var button = document.getElementById("my-form-button");
    boton.addEventListener('click', aceptarTerminos);

    function obtenerDatosLocalStorage() {
        let decision = localStorage.Terminos;
        if(decision === 'aceptado') {
          aceptarTerminos();
        }
    }
    obtenerDatosLocalStorage();
    function aceptarTerminos() {
        contenedor.style.display = "none";
        localStorage.Terminos = 'aceptado';
    }
    function limpiarStorage() {
        localStorage.clear();
    }

    function success() {
        form.reset();
        button.style = "display: none ";
        status.innerHTML = "Gracias por ponerte en contacto con nosotros, en breve te estaremos respondiendo!";
      }
  
      function error() {
        status.innerHTML = "Oops! There was a problem.";
      }
  
      // handle the form submission event
  
      form.addEventListener("submit", function(ev) {
        ev.preventDefault();
        var data = new FormData(form);
        ajax(form.method, form.action, data, success, error);
    });
    
    // helper function for sending an AJAX request
});

function ajax(method, url, data, success, error) {
    var xhr = new XMLHttpRequest();
    xhr.open(method, url);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = function() {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        success(xhr.response, xhr.responseType);
      } else {
        error(xhr.status, xhr.response, xhr.responseType);
      }
    };
    xhr.send(data);
  }